import Vue from 'vue'
import DmMask from './mask'

const MaskConstructer = Vue.extend(DmMask)
const maskInstance = new MaskConstructer().$mount()

const Mask = options => {
  maskInstance.visible = options.visible || false
  if (options.el) {
    options.el.appendChild(maskInstance.$el)
  } else {
    document.body.appendChild(maskInstance.$el)
  }

  return new Promise((resolve, reject) => {
    Object.assign(maskInstance, {
      ...options,
      resolve,
      reject,
      onClickOverlay: () => resolve()
      // onClickOverlay: closableOverlay => closableOverlay ? resolve() : reject()
    })
  })
}

Mask.toggle = options => Mask({
  visible: !maskInstance.visible,
  ...options
})

Mask.render = options => Mask({
  visible: true,
  ...options
})

Mask.destory = options => Mask({
  visible: false,
  ...options
})

export default Mask



