<template>
  <div style="padding-top: 51px;">
    <pr-row>
      <pr-col span="12">
        <section>
          <h1>示例演示</h1>
          <hr/>
          <h2>have a try!</h2>
          <div class="pr-form-group">
            <div class="pr-form-item">
              <span class="pr-form-item-label">
                <i class="pr-required"></i>
                用户名：
              </span>
              <span class="pr-form-item-input">
                <pr-input
                  v-model="formData.username"
                  type="text"
                  placeholder="请输入2-10字符"
                  width="300"
                />
              </span>
            </div>

            <div class="pr-form-item">
              <span class="pr-form-item-label">
                <i class="pr-required"></i>
                邮箱：
              </span>
              <span class="pr-form-item-input">
                <pr-input
                  v-model="formData.email"
                  type="text"
                  placeholder="请输入常用邮箱地址"
                  width="300"
                />
              </span>
            </div>

            <div class="pr-form-item">
              <span class="pr-form-item-label">
                <i class="pr-required"></i>
                手机号：
              </span>
              <span class="pr-form-item-input">
                <pr-input
                  v-model="formData.mobile"
                  type="text"
                  placeholder="请输入移动手机号"
                  width="300"
                />
              </span>
            </div>

            <div class="pr-form-item">
              <span class="pr-form-item-label"> </span>
              <span class="pr-form-item-input">
                <pr-button
                  type="primary"
                  @click="handleSave"
                >
                  保存
                </pr-button>
              </span>
            </div>
          </div>
          <div style="padding-left: 80px;">
            <p>信息：{{msg}}</p>
            <p>校验未通过字段：{{validator.validateKey}}</p>
            <p>校验结果：{{validator.validateResult}}</p>
          </div>
        </section>
      </pr-col>
      <pr-col span="12">
        <Example />
      </pr-col>
    </pr-row>
  </div>
</template>

<script>
import Example from "../docs/validator/index/example.md";
export default {
  components: {
    Example
  },
  data() {
    return {
      formData: {
        username: "",
        email: "",
        mobile: ""
      },
      validator: new this.$Validator({
        username: [
          {
            required: true,
            message: "请输入username"
          }
        ],
        email: [
          {
            required: true,
            message: "请输入邮箱地址"
          },
          {
            type: "email",
            message: "请输入正确邮箱地址"
          }
        ],
        mobile: [
          {
            required: true,
            message: "请输入手机号"
          },
          {
            type: "chinaMobile",
            message: "输入的号码非中国移动手机号"
          }
        ]
      }),
      msg: ""
    };
  },
  methods: {
    handleSave() {
      this.validator
        .validate(this.formData)
        .then(() => {
          this.msg = "校验通过";
        })
        .catch(msg => {
          this.msg = msg;
        });
    }
  }
};
</script>

<style lang="less">
.pr-col-12 {
  max-height: calc(100vh - 91px);
  overflow: auto;
}
.pr-form-group {
  margin: 20px auto;
}

.pr-form-item-label {
  width: 120px;
}

.demo-container {
  height: auto;
}
.docs-trans {
  display: none;
}
</style>



