<template>
  <div :class="[
        type === 'textarea' ? 'pr-textarea' : 'pr-input',
        `pr-input-${size}`, 
        {
            'pr-disabled': disabled,
            'has-addon': addon
        }
    ]"
    :style="{'width': `${width}px`}">
    <!-- textarea -->
    <template v-if="type === 'textarea'">
      <span v-if="placeholder && isIE9"
        v-show="showPlaceholder"
        class="pr-input-placeholder"
        @click="handleFocus">
        {{placeholder}}
      </span>
      <textarea ref="dyInput"
        :value="propValue"
        :placeholder="isIE9 ? null : placeholder"
        :maxlength="maxlength"
        :disabled="disabled"
        v-bind="$attrs"
        @compositionstart="startCHN"
        @compositionend="endCHN"
        v-on="inputActions" />
      </template>
    <!-- input -->
    <template v-else>
      <span v-if="addon"
      class="pr-input-addon">
        {{addon}}
      </span>
      <span v-if="prefixIcon"
        class="pr-icon-fix pr-input-prefix-icon"
        @click="prefixAction">
        <i :class="`pr-icon iconfont icon-${prefixIcon}`"></i>
      </span>
      <span v-if="placeholder && isIE9"
        v-show="showPlaceholder"
        class="pr-input-placeholder" @click="handleFocus">
        {{placeholder}}
      </span>
      <input ref="dyInput" 
        :type="type"
        :class="{'pr-input-suffix': suffixIcon, 'pr-input-clear': clearable}"
        :value="propValue"
        :placeholder="isIE9 ? null : placeholder"
        :autocomplete="autoComplete"
        :maxlength="maxlength"
        :disabled="disabled"
        v-bind="$attrs"
        @compositionstart="startCHN"
        @compositionend="endCHN"
        v-on="inputActions" />
      <span v-if="suffixIcon"
        class="pr-icon-fix pr-input-suffix-icon"
        @click="suffixAction">
        <i :class="`pr-icon iconfont icon-${suffixIcon}`"></i>
      </span>
      <i v-if="clearable && !disabled"
        v-show="propValue !== ''"
        class="pr-icon iconfont icon-danger"
        @click="clearValue"></i>
    </template>
  </div>
</template>

<script type="text/javascript">
/**
 * input | textarea
 * @example
 * <pr-input v-model="value" />
 */
import '../theme-default/input.less'
import { isIE } from '@/utils/common'
export default {
  name: 'pr-input',
  inheritAttrs: false,
  model: {
    prop: 'value',
    event: 'input'
  },
  props: {
    addon: {
      type: String
    },
    // 类型，包括'text'、'textarea'等原生属性
    type: {
      type: String,
      default: 'text'
    },
    // 输入框宽度
    width: [Number, String],
    // placeholder
    placeholder: {
      type: String,
      default: null
    },
    // 自动填充
    autoComplete: {
      type: String,
      default: 'off'
    },
    // 为了兼容ie9。。。好气啊
    maxlength: [Number, String],
    // 输入框前后图标
    prefixIcon: {
      type: String,
      default: null
    },
    suffixIcon: {
      type: String,
      default: null
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    /**
     * 大小 default、small、large
     * default auto
     * small 180 => 150
     * large 250 => 180
     */
    size: {
      type: String,
      default: 'default'
    },
    // 输入框值
    value: [String, Number],
    // 是否可清空
    clearable: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // 输入框值
      propValue: this.value,
      isInputData: false,
      // 输入框组合
      composing: false,
      isInputNumber: false
    }
  },
  computed: {
    isIE9() {
      return navigator.userAgent.indexOf('MSIE 9.0') > 0
    },
    /**
     * vue 原生行为
     */
    inputActions() {
      return Object.assign({}, this.$listeners, {
        input: event => {
          if (this.type === 'number') {
            if (
              !this.isInputNumber &&
              event.inputType !== 'deleteContentBackward'
            ) {
              event.target.value = this.value
            }
          }
          if (
            (event.target.validity && event.target.validity.valid) ||
            this.isIE9
          ) {
            let value = event.target.value
            if (value.length > this.maxlength) return
            this.$emit('input', value)
          }
        },
        keypress: event => {
          this.isInputNumber = /[\d]/.test(String.fromCharCode(event.keyCode))
        }
      })
    },
    /**
     * 是否显示placeholder
     */
    showPlaceholder() {
      if (this.composing) {
        return false
      }
      return !this.propValue && this.propValue !== 0
    }
  },
  methods: {
    /**
     * 前缀图标事件
     */
    prefixAction(e) {
      if (this.disabled) return
      this.$emit('prefix-action', e)
    },
    /**
     * 后缀图标事件
     */
    suffixAction(e) {
      if (this.disabled) return
      this.$emit('suffix-action', e)
    },
    /**
     * 清空数值
     */
    clearValue(e) {
      this.propValue = ''
      this.$emit('input', '')
      this.$emit('clear')
    },
    /**
     * 解决ie9下兼容问题
     */
    handleFocus() {
      this.$refs.dyInput.focus()
    },
    /**
     * 处理中文输入法
     */
    //  @compositionstart="startCHN" @compositionend="endCHN"
    startCHN(e) {
      this.composing = true
    },
    endCHN(e) {
      this.composing = false
    }
  },
  watch: {
    value(newValue) {
      this.propValue = newValue
    }
  }
}
</script>



