<template><section><h1>实例属性--validateKey</h1>
<hr>
<p>（对方直接向你丢来一个应用场景）</p>
<p><em>当需要定位校验未通过的字段时，validateKey 属性就发挥了它的作用</em></p>
<div class="demo-block">
<p>帐号：<pr-input v-model="account" placeholder="请输入帐号" type="text" @input="errorIn.account = false;msg = ''"></pr-input></p>
<p v-if="errorIn.account" style="color: red;">{{msg}}</p>
<p>密码：<pr-input v-model="password" placeholder="请输入密码" type="password" @input="errorIn.password = false;msg = ''"></pr-input></p>
<p v-if="errorIn.password" style="color: red;">{{msg}}</p>
<p><pr-button @click="login">登录</pr-button></p>
</div>
<demo-block>
                        <div slot="desc">
  帐号：

    {{msg}}

  密码：

    {{msg}}

  登录

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  帐号：<span class="hljs-tag">&lt;<span class="hljs-name">pr-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"account"</span>
          <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入帐号"</span>
          <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span>
          <span class="hljs-attr">@input</span>=<span class="hljs-string">"errorIn.account = false;msg = ''"</span> /&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">p</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"errorIn.account"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"color: red;"</span>&gt;</span>{{msg}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

  密码：<span class="hljs-tag">&lt;<span class="hljs-name">pr-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"password"</span>
          <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入密码"</span>
          <span class="hljs-attr">type</span>=<span class="hljs-string">"password"</span>
          <span class="hljs-attr">@input</span>=<span class="hljs-string">"errorIn.password = false;msg = ''"</span> /&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">p</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"errorIn.password"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"color: red;"</span>&gt;</span>{{msg}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">pr-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"login"</span>&gt;</span>登录<span class="hljs-tag">&lt;/<span class="hljs-name">pr-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">account</span>: <span class="hljs-string">""</span>,
      <span class="hljs-attr">password</span>: <span class="hljs-string">""</span>,
      <span class="hljs-attr">msg</span>: <span class="hljs-string">""</span>,
      <span class="hljs-attr">errorIn</span>: {
        <span class="hljs-attr">account</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">password</span>: <span class="hljs-literal">false</span>
      },
      <span class="hljs-attr">validator</span>: <span class="hljs-keyword">new</span> <span class="hljs-keyword">this</span>.$Validator({
        <span class="hljs-attr">account</span>: [
          {
            <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>,
            <span class="hljs-attr">message</span>: <span class="hljs-string">"请输入帐号"</span>
          }
        ],
        <span class="hljs-attr">password</span>: [
          {
            <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>,
            <span class="hljs-attr">message</span>: <span class="hljs-string">"请输入密码"</span>
          }
        ]
      })
    };
  },
  <span class="hljs-attr">methods</span>: {
    login() {
      <span class="hljs-keyword">this</span>.validator
        .validate({
          <span class="hljs-attr">account</span>: <span class="hljs-keyword">this</span>.account,
          <span class="hljs-attr">password</span>: <span class="hljs-keyword">this</span>.password
        })
        .then(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {})
        .catch(<span class="hljs-function"><span class="hljs-params">msg</span> =&gt;</span> {
          <span class="hljs-keyword">let</span> validateKey = <span class="hljs-keyword">this</span>.validator.validateKey;
          <span class="hljs-keyword">this</span>.errorIn[validateKey] = <span class="hljs-literal">true</span>;
          <span class="hljs-keyword">this</span>.msg = msg;
        });
    }
  }
};
</code></pre>
</div></demo-block>

</section></template>

<script>
export default{
  data(){
    return {
      account: '',
      password: '',
      msg: '',
      errorIn: {
        account: false,
        password: false
      },
      validator: new this.$Validator({
        account: [{
          required: true,
          message: '请输入帐号'
        }],
        password: [{
          required: true,
          message: '请输入密码'
        }]
      })
    }
  },
  methods: {
    login(){
      this.validator.validate({
        account: this.account,
        password: this.password
      }).then(() => {

      }).catch(msg => {
        let validateKey = this.validator.validateKey
        this.errorIn[validateKey] = true
        this.msg = msg
      })
    }
  }
}
</script>


