<template>
  <div id="app">
    <pr-row class="navigation_row">
      <pr-col span="4">
        <div v-show="!$root.isMobile"
          class="navigation_title">
          <span style="margin-right: -6px;font-size: 12px;color: #fbdacf;">gnoz</span>
          Validator
        </div>
        <div v-show="$root.isMobile"
          class="navigation_menu">
          <pr-icon type="navicon"
            @click="$root.isMenuVisible = !$root.isMenuVisible" />
        </div>
      </pr-col>
      <pr-col span="20">
        <ul class="navigation_list">
          <router-link tag="li"
            to="/example"
            exact>
            实例
          </router-link>
          <router-link tag="li"
            to="/doc">
            文档
          </router-link>
          <li>
            <a href="javascript:void(0)">v0.2.2</a>
          </li>
        </ul>
      </pr-col>
    </pr-row>
    <router-view />
  </div>
</template>

<script>
import './assets/style/reset.less'
import './assets/style/common.less'
export default {
  name: 'App'
}
</script>

<style lang="less">
.navigation {
  &_row {
    position: fixed;
    left: 0;
    z-index: 99;
    width: 100%;
    background: #fff;

    .pr-col {
      margin-bottom: 0;
    }
  }

  &_title,
  &_menu {
    padding: 15px 0;
    text-align: center;
    color: coral;
  }

  &_menu {
    font-size: 14px;
  }

  &_list {
    float: right;

    li {
      display: inline-block;
      padding: 15px;

      &:hover {
        cursor: pointer;
      }
    }
  }
}
</style>



