<template>
  <label class="pr-checkbox"
    :class="{'pr-checked': isChecked, 'pr-disabled': disabled}">
    <span class="pr-checkbox-input">
      <input type="checkbox"
        :value="data"
        :disabled="disabled"
        v-bind="$attrs"
        @change="handleChange" />
      <i class="pr-icon iconfont"
        :class="isChecked ? 'icon-checkbox-check' : 'icon-checkbox'"></i>
    </span>
    <span class="pr-checkbox-text">
      <slot></slot>
    </span>
  </label>
</template>

<script type="text/javascript">
/**
 * checkbox
 * @example
 * isChecked => false
 * <pr-checkbox :value="true">男</pr-checkbox> => checked
 * <pr-checkbox v-model="isChecked">女</pr-checkbox> => unChecked
 * more see checkbox-group
 */
import '../theme-default/checkbox.less'
export default {
  name: 'pr-checkbox',
  inheritAttrs: false,
  model: {
    prop: 'value',
    event: 'change'
  },
  inject: {
    changeGroupValue: {
      default: null
    }
  },
  props: {
    /**
     * 非 checkbox-group 下 v-model 值
     */
    value: {
      type: Boolean,
      default: false
    },
    /**
     * checkbox 值 (checkbox-group 下)
     */
    data: {
      type: [String, Number, Boolean]
    },
    /**
     * checkbox 是否禁用状态
     */
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      /**
       * checkbox-group v-model值存储
       */
      groupValue: []
    }
  },
  computed: {
    /**
     * 是否有 checkbox-group
     */
    isGroup() {
      return typeof this.changeGroupValue === 'function'
      // let parent = this.$parent
      // while (parent) {
      //   if (parent.$options._componentTag !== 'pr-checkbox-group') {
      //     parent = parent.$parent
      //   } else {
      //     return true
      //   }
      // }
      // return false
    },
    /**
     * 是否 checked
     */
    isChecked() {
      return this.isGroup
        ? (this.groupValue || []).includes(this.data)
        : this.value
    }
  },
  methods: {
    /**
     * checked or not checked
     */
    handleChange(ev) {
      if (this.disabled || this.$parent.disabled) return
      let lable = this.$slots.default ? this.$slots.default[0].text : null
      let extra = { ...this.$attrs }
      if (this.isGroup) {
        this.changeGroupValue(
          ev.target._value,
          this.groupValue.includes(this.data)
        )
        // if (this.groupValue.indexOf(this.data) === -1) {
        //   this.$parent.$emit(
        //     'change',
        //     (this.$parent.value || []).concat(ev.target._value),
        //     extra
        //   )
        // } else {
        //   this.$parent.$emit(
        //     'change',
        //     this.$parent.value.filter(item => item !== ev.target._value),
        //     extra
        //   )
        // }
      } else {
        this.$emit('change', !this.value, this.data, lable)
      }
    }
  },
  mounted() {
    if (this.isGroup) {
      this.groupValue = [...this.$parent.value] || []
    }
  },
  watch: {
    '$parent.value'() {
      this.groupValue = [...this.$parent.value]
    }
  }
}
</script>



