import "babel-polyfill";
import Vue from "vue";
import router from "./router";
import App from "./App";
import Components from "../packages";

import DemoBlock from "./components/DemoBlock";

import Validator from "@gnoz/validator";

Vue.prototype.$Validator = Validator;

Vue.use(Components);
Vue.component("demo-block", DemoBlock);

import hljs from "highlight.js";
import "highlight.js/styles/atelier-dune-dark.css";
Vue.directive("highlight", el => {
  let blocks = el.querySelectorAll("pre code");
  blocks.forEach(block => {
    hljs.highlightBlock(block);
  });
});

let isMobile = document.body.clientWidth <= 767.98;

/* eslint-disable no-new */
new Vue({
  el: "#app",
  data() {
    return {
      isMobile,
      isMenuVisible: !isMobile
    };
  },
  mounted() {
    window.addEventListener("resize", e => {
      this.isMobile = document.body.clientWidth <= 767.98;
      this.isMenuVisible = !this.isMobile;
    });
  },
  router,
  components: {
    App
  },
  template: "<App/>"
});



