<template>
  <div class="pr-collapse">
    <slot></slot>
  </div>
</template>

<script>
import '../theme-default/collapse.less'
export default {
  name: 'pr-collapse',
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: [String, Number, Boolean, Array],
      default: null
    },
    accordion: {
      type: Boolean,
      default: false
    }
  }
}
</script>



