<template>
  <div class="pr-popup">
    <transition :name="transitionName">
      <div v-if="value"
        class="pr-popup-content"
        :class="setTransitionCls">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
import '../theme-default/popup.less'
import Mask from './mask'

// 动画索引
const transitionNameMap = {
  top: 'slide-in-top',
  right: 'slide-in-right',
  bottom: 'slide-in-bottom',
  left: 'slide-in-left'
}

export default {
  name: 'pr-popup',
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    // 组件是否显示
    value: {
      type: Boolean,
      default: false
    },
    // 是否显示蒙版
    overlay: {
      type: Boolean,
      default: true
    },
    // 是否点击蒙版关闭
    closableOverlay: {
      type: Boolean,
      default: false
    },
    // 弹窗位置 (top、right、bottom、left)
    position: {
      type: String,
      default: 'center'
    }
  },
  data() {
    return {
      transitionName: transitionNameMap[this.position] || 'fade',
      show: this.value
    }
  },
  computed: {
    setTransitionCls() {
      return {
        'pr-popup-center': this.show && this.position === 'center',
        'pr-popup-bottom': this.show && this.position === 'bottom',
        'pr-popup-top': this.show && this.position === 'top',
        'pr-popup-left': this.show && this.position === 'left',
        'pr-popup-right': this.show && this.position === 'right'
      }
    }
  },
  watch: {
    /**
     * 监听蒙版是否显示
     */
    value(newValue) {
      // Mask.edit(node, () => {})
      this.show = newValue
      if (newValue) {
        this.overlay &&
          Mask.render({
            closableOverlay: this.closableOverlay,
            el: this.$el
          }).then(() => {
            this.show = false
            this.$emit('change', false)
          })
      } else {
        this.overlay && Mask.destory()
      }
      // if (this.overlay && this.value) {
      //   this.show = !this.show
      //   Mask.toggle({
      //     closableOverlay: this.closableOverlay,
      //     el: this.$el
      //   }).then(() => {
      //     this.show = false
      //     this.$emit('change', false)
      //   })
      // }
    }
  }
}
</script>



