<template>
  <pr-popup v-model="visible"
    position="top"
    v-if="!destroy">
    <div class="pr-dialog">
      <div v-if="title"
        class="pr-dialog-header">
        {{ title }}
      </div>
      <div class="pr-dialog-body">
        <div class="pr-dialog-body-tip">
          <pr-icon type="info" />
          <span class="pr-help">提示</span>
        </div>
        <div class="pr-dialog-body-msg">
          <slot>
            {{ message }}
          </slot>
        </div>
      </div>
      <div class="pr-dialog-footer pr-hairline-top">
        <pr-button v-if="showCancelBtn"
          class="pr-dialog-cancel"
          @click="handleDialog(false)">取消</pr-button>
        <pr-button class="pr-dialog-comfirm"
          type="danger"
          @click="handleDialog(true)">确认</pr-button>
      </div>
    </div>
  </pr-popup>
</template>

<script>
import '../theme-default/dialog.less'
import Button from '../button/button'
import Icon from '../icon/icon'
import Popup from '../popup/popup'

export default {
  name: 'pr-dialog',
  components: {
    'pr-button': Button,
    'pr-icon': Icon,
    'pr-popup': Popup
  },
  props: {
    title: [String, Number],
    message: [String, Number]
  },
  data() {
    return {
      visible: false,
      showCancelBtn: false,
      destroy: false
    }
  },
  methods: {
    handleDialog(isComfirm) {
      this.callback && this.callback(isComfirm)
      this.visible = false
      this.$nextTick(() => (this.destroy = true))
    }
  }
}
</script>



