import Vue from 'vue'
import DmDialog from './dialog'

const Dialog = (options = {
  el: null
}) => {
  let {
    el,
    title,
    message,
    visible
  } = options
  let DialogConstructer = Vue.extend(DmDialog)
  let DialogInstance = null
  if (!visible) return
  if (el) {
    DialogInstance = new DialogConstructer().$mount(el)
  } else {
    DialogInstance = new DialogConstructer().$mount()
    document.body.appendChild(DialogInstance.$el)
  }

  DialogInstance.visible = visible
  DialogInstance.title = title
  DialogInstance.message = message

  return visible && new Promise((resolve, reject) => {
    Object.assign(DialogInstance, {
      ...options,
      resolve,
      reject,
      callback: isComfirm => (isComfirm ? resolve() : reject())
    })
  })
}

Dialog.alert = options => Dialog({
  showCancelBtn: false,
  visible: true,
  ...options
})

Dialog.comfirm = options => Dialog({
  showCancelBtn: true,
  visible: true,
  ...options
})

Dialog.destory = options => Dialog({
  visible: false,
  ...options
})

// Dialog.install = () => {
//   Vue.use(DmDialog)
// }

export default Dialog



