<template>
  <div class="pr-col"
    :class="{[`pr-col-${span}`]: span, [`pr-col-offset-${offset}`]: offset}"
    :style="calcGutter">
    <slot></slot>
  </div>
</template>

<script>
import '../theme-default/col.less'
export default {
  name: 'pr-col',
  props: {
    span: {
      type: [String, Number],
      default: null
    },
    offset: {
      type: [String, Number],
      default: null
    }
  },
  computed: {
    calcGutter() {
      let { gutter } = this.$parent
      if (gutter) {
        return {
          paddingLeft: gutter / 2 + 'px',
          paddingRight: gutter / 2 + 'px'
        }
      }
      return {}
    }
  }
}
</script>



