import Vue from "vue";
import VueRouter from "vue-router";

import gnozValidatorDocMap from "@/gnozValidatorDoc";

import Index from "../views/index";
import Container from "../views/container";

import Resume from "../docs/validator/resume/index.md";
import QuickStart from "../docs/validator/resume/quick.md";
import CustomRule from "../docs/validator/resume/customRule.md";
import ChangeLog from "../docs/validator/resume/logs.md";

import RuleRequired from "../docs/validator/rules/required.md";
import RuleMax from "../docs/validator/rules/max.md";
import RuleMin from "../docs/validator/rules/min.md";
import RuleValidator from "../docs/validator/rules/validator.md";
import RuleType from "../docs/validator/rules/type.md";

import InstanceValidateKey from "../docs/validator/instance/validateKey.md";
import InstanceValidateResult from "../docs/validator/instance/validateResult.md";

import ExtendsConfig from "../docs/validator/extends/config.md";
import ExtendsFunctions from "../docs/validator/extends/functions.md";

Vue.use(VueRouter);

const docsMap = {
  Resume,
  QuickStart,
  CustomRule,
  ChangeLog,
  RuleRequired,
  RuleMax,
  RuleMin,
  RuleValidator,
  RuleType,
  InstanceValidateKey,
  InstanceValidateResult,
  ExtendsConfig,
  ExtendsFunctions
};

let childrenRoutes = [];
for (let i = 0; i < gnozValidatorDocMap.length; i++) {
  let children = gnozValidatorDocMap[i].children;
  for (let c = 0; c < children.length; c++) {
    let key = children[c].component;
    childrenRoutes.push({
      path: children[c].path,
      component: docsMap[key]
    });
  }
}

const routes = [
  {
    path: "/",
    redirect: "/example"
  },
  {
    path: "/example",
    component: Index
  },
  {
    path: "/doc",
    component: Container,
    children: [
      {
        path: "",
        redirect: "resume"
      },
      ...childrenRoutes
    ]
  }
];

export default new VueRouter({
  routes,
  linkActiveClass: "router-active",
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return {
        x: 0,
        y: 0
      };
    }
  }
});



