<template>
  <div class="pr-tree">
    <div class="pr-tree-wrapper">
      <pr-tree-node :node-key="nodeKey"
        :data-source="dataSource"
        :indent="indent"
        :key-maps="realityKeyValue"
        :is-lazy-load="isLazyLoad"
        :expand-root-key="isLazyLoad"
        :lazy-load="lazyLoad"
        :show-checkbox="showCheckbox"
        :check-strictly="checkStrictly"
        :is-node-checked="isNodeChecked"
        @nodeClick="handleClickNode"
        @nodeCheck="handleCheckNode" />
    </div>
  </div>
</template>

<script>
import '../theme-default/tree.less'
import DyTreeNode from './tree-node.vue'
export default {
  name: 'pr-tree',
  components: {
    'pr-tree-node': DyTreeNode
  },
  props: {
    dataSource: {
      type: Array,
      default() {
        return []
      }
    },
    nodeKey: {
      type: String,
      default: 'id',
      required: true
    },
    keyMaps: Object,
    // 缩进
    indent: {
      type: Number,
      default: 12
    },
    lazyLoad: Function,
    // 是否展示复选框
    showCheckbox: {
      type: Boolean,
      default: false
    },
    // 父子级间复选框是否互不关联
    checkStrictly: {
      type: Boolean,
      default: false
    },
    isNodeChecked: {
      type: Boolean,
      default: false
    }
  },
  data() {
    let defaultKeyValue = {
      label: 'label',
      disabled: 'disabled',
      isLeaf: 'isLeaf',
      children: 'children'
    }
    return {
      realityKeyValue: Object.assign(defaultKeyValue, this.keyMaps),
      isLazyLoad: typeof this.lazyLoad === 'function',
      // 复选框选中(true)的节点
      checkedNodes: {
        // uniqueKey: true or false
      },
      // 展开(true)的节点
      unfoldedNodes: {
        // uniqueKey: true or false
      }
    }
  },
  provide() {
    return {
      checkedNodes: this.checkedNodes,
      unfoldedNodes: this.unfoldedNodes,
      setCheckedNodes: this.setCheckedNodes,
      setUnfoldedNodes: this.setUnfoldedNodes
    }
  },
  methods: {
    // 设置节点是否选中状态
    setCheckedNodes(nodes, isChecked = true) {
      let nodeKey = this.nodeKey
      for (let i = 0; i < nodes.length; i++) {
        let node = nodes[i]
        this.$set(this.checkedNodes, node[nodeKey], isChecked)
      }
    },
    // 设置节点是否展开状态
    setUnfoldedNodes(nodes, isUnfold = true) {
      let nodeKey = this.nodeKey
      for (let i = 0; i < nodes.length; i++) {
        let node = nodes[i]
        this.$set(this.unfoldedNodes, node[nodeKey], isUnfold)
      }
    },
    handleClickNode(node, index) {
      this.$emit('nodeClick', node, index)
    },
    handleCheckNode(isChecked, node) {
      this.$emit('nodeCheck', isChecked, node)
    }
  }
}
</script>



