<template>
  <div class="pr-checkbox-group"
    :class="{'pr-checkbox-disabled': disabled}">
    <slot></slot>
  </div>
</template>

<script type="text/javascript">
/**
 * checkbox-group
 * @example
 * checkedList => ['男', '女']
 * <pr-checkbox-group v-model="checkedList">
 *    <pr-checkbox data="男">男</pr-checkbox> => checked
 *    <pr-checkbox data="女">女</pr-checkbox> => checked
 *    <pr-checkbox data="人妖">人妖</pr-checkbox> => unChecked
 * </pr-checkbox-group>
 */
import '../theme-default/checkbox-group.less'
export default {
  name: 'pr-checkbox-group',
  model: {
    prop: 'value',
    event: 'change'
  },
  provide() {
    return {
      changeGroupValue: this.changeValue
    }
  },
  props: {
    /**
     * v-model值
     */
    value: {
      type: Array,
      default() {
        return []
      }
    },
    /**
     * 是否全部禁用
     */
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    changeValue(value, isExisted = false) {
      let currentValue = [...this.value]
      if (isExisted) {
        currentValue = currentValue.filter(item => item !== value)
      } else {
        currentValue = currentValue.concat(value)
      }
      this.$emit('change', currentValue)
    }
  }
}
</script>



