<template>
  <div class="pr-row"
    :class="{'clearfix': !flexable, 'pr-row-flex': flexable, [`pr-row-justify-${justify}`]: flexable, [`pr-row-align-${align}`]: flexable}">
    <slot></slot>
  </div>
</template>

<script>
import '../theme-default/row.less'
export default {
  name: 'pr-row',
  props: {
    gutter: {
      type: [String, Number],
      default: 0
    },
    // 是否开启flex布局
    flexable: {
      type: Boolean,
      default: false
    },
    // flex布局justify-content属性
    justify: {
      type: String,
      default: 'flex-start'
    },
    // flex布局align-content属性
    align: {
      type: String,
      default: 'flex-start'
    }
  }
}
</script>



