<template>
  <div class="pr-tooltip"
    :class="{ [`pr-tooltip-show pr-tooltip-${placement}`]: showTooltip }"
    @mouseenter="showTooltip = true"
    @mouseleave="showTooltip = false">
    <div class="pr-tooltip-target">
      <slot></slot>
    </div>
    <div v-show="showTooltip"
      class="pr-tooltip-context">
      <slot name="context">
        <div v-html="context"></div>
      </slot>
    </div>
  </div>
</template>

<script>
import '../theme-default/tooltip.less'
export default {
  name: 'pr-tooltip',
  props: {
    context: null,
    placement: {
      type: String,
      validator(value) {
        const placementMap = [
          'top-left',
          'top',
          'top-right',
          'left',
          'right',
          'bottom-left',
          'bottom',
          'bottom-right'
        ]
        return placementMap.includes(value)
      },
      default: 'top'
    }
  },
  data() {
    return {
      showTooltip: false
    }
  }
}
</script>



