<template>
  <div class="pr-tree-node">
    <div class="pr-tree-node_wrapper">
      <div class="pr-tree-node_row"
        v-for="node in treeNodeData"
        :key="node[nodeKey]"
        :class="
          handleJudgeIsUnCollapsed(node[nodeKey])
            ? 'pr-unfold'
            : 'pr-fold'
        "
        :style="{ paddingLeft: `${indent}px` }">
        <span class="pr-tree-node_row-cell pr-tree-node_row-caret"
          :class="{ 'pr-rotate': handleJudgeIsUnCollapsed(node[nodeKey]) }"
          @click="handleToggleTreeNode(node[nodeKey], node)">
          <pr-icon type="caret-right"
            v-show="!loadingChildNodes"
            v-if="!node[keyMaps['isLeaf']]" />
          <pr-icon type="loading"
            v-show="loadingChildNodes" />
        </span>
        <span v-if="showCheckbox"
          class="pr-tree-node_row-checkbox">
          <DyCheckbox v-model="checkedNodes[node[nodeKey]]"
            @change="handleChangeNodeCheck($event, node)" />
        </span>
        <span class="pr-tree-node_row-cell pr-tree-node_row-label"
          @click="handleClickNode(node, treeNodeIndex)">
          {{ node[keyMaps.label] }}
        </span>
      </div>
      <div class="pr-tree-node_row-height"
        :style="{height: calcTreeNodeHeight}"
        v-for="node in lazyLoadTreeNodeData"
        :key="node[nodeKey]">
        <pr-tree-node :key="node[nodeKey]"
          :node-key="nodeKey"
          :data-source="[node]"
          :key-maps="keyMaps"
          :indent="calcIndent"
          :unfold-all="unfoldAll"
          :lazy-load="lazyLoad"
          :level="nodeLevel + 1"
          :show-checkbox="showCheckbox"
          :check-strictly="checkStrictly"
          :is-node-checked="checkStrictly ? false : checkedNodes[node[nodeKey]]"
          @nodeCheck="handleChangeNodeCheck"
          @nodeClick="handleClickNode" />
      </div>
    </div>
  </div>
</template>

<script>
import DyIcon from '../icon/icon'
import DyCheckbox from '../checkbox/checkbox'
export default {
  name: 'pr-tree-node',
  components: {
    DyIcon,
    DyCheckbox
  },
  inject: [
    'checkedNodes',
    'unfoldedNodes',
    'setCheckedNodes',
    'setUnfoldedNodes'
  ],
  props: {
    // 唯一键
    nodeKey: String,
    // 数据源
    dataSource: {
      type: Array,
      default() {
        return []
      }
    },
    // 键值关系映射
    keyMaps: Object,
    // 懒加载
    lazyLoad: Function,
    // 是否懒加载
    isLazyLoad: {
      type: Boolean,
      default: false
    },
    // 是否展开根节点
    expandRootKey: {
      type: Boolean,
      default: false
    },
    // 树节点缩进
    indent: Number,
    // 树节点是否展开
    unfoldAll: {
      type: Boolean,
      default: false
    },
    // 树节点层级
    level: {
      type: Number,
      default: 1
    },
    // 是否展示复选框
    showCheckbox: Boolean,
    // 父子级间复选框是否互不关联
    checkStrictly: Boolean,
    // 树节点是否选中状态
    isNodeChecked: Boolean
  },
  data() {
    let key = this.nodeKey
    return {
      // 树节点数据
      treeNodeData: [...this.dataSource],
      // 利用键(nodeKey)值对关系映射懒加载数据
      lazyLoadTreeNodeData: [],
      // 利用键(nodeKey)值对关系映射复选框状态
      nodeCheck: this.isNodeChecked,
      // 子树节点树选中状态计数（如计数等于子树节点则该节点选中）
      childrenNodeCheckedCount: this.isNodeChecked ? this.dataSource.length : 0,
      // 树节点展开收缩索引
      unCollapsedIndex: this.unfoldAll
        ? this.dataSource.map(item => item[key])
        : [],
      // 树节点层级
      nodeLevel: this.level,
      // 懒加载展开节点时loading状态
      loadingChildNodes: false
    }
  },
  computed: {
    // 计算缩进
    calcIndent() {
      return (this.indent / this.level) * (this.level + 1)
    },
    // 计算节点高度
    calcTreeNodeHeight() {
      const { treeNodeData, nodeKey } = this
      return this.handleJudgeIsUnCollapsed(treeNodeData[0][nodeKey])
        ? 'auto'
        : 0
    }
  },
  methods: {
    // 收缩、开展树节点
    handleToggleTreeNode(nodeKeyValue, treeNode) {
      if (treeNode[this.keyMaps['isLeaf']]) return
      // this.parentNodeKeyValueData = key
      let isUnfold = !this.unfoldedNodes[nodeKeyValue]
      if (isUnfold) {
        console.log('rootExpaned')
        // this.unCollapsedIndex.push(key)
        this.setUnfoldedNodes([treeNode], true)
        this.handleLazyLoad({
          ...treeNode,
          level: this.nodeLevel,
          __VNode: this
        })
      } else {
        this.setUnfoldedNodes([treeNode], false)
        // this.unCollapsedIndex = this.unCollapsedIndex.filter(
        //   item => item !== key
        // )
      }
    },
    // 判断是否展开当前树节点
    handleJudgeIsUnCollapsed(nodeKeyValue) {
      return !!this.unfoldedNodes[nodeKeyValue]
      // return this.unCollapsedIndex.includes(index)
    },
    // 树节点懒加载
    handleLazyLoad(treeNode) {
      if (
        typeof this.lazyLoad !== 'function' ||
        this.lazyLoadTreeNodeData.length
      )
        return
      new Promise((resolve, reject) => {
        this.loadingChildNodes = true
        this.lazyLoad(treeNode, resolve)
      }).then(data => {
        this.loadingChildNodes = false
        if (treeNode.level === 0) {
          this.treeNodeData = data.map(item => ({
            parentNode: { ...treeNode },
            data: { ...item, isRoot: true },
            ...item
          }))
          if (this.expandRootKey) {
            this.handleToggleTreeNode(
              this.treeNodeData[0][this.nodeKey],
              this.treeNodeData[0]
            )
          }
        } else {
          // 如果当前懒加载数据不为空则保留当前懒加载数据
          if (this.lazyLoadTreeNodeData.length) return
          // 当懒加载数据为空时说明当前节点为叶子节点
          if (data.length === 0) {
            let isLeafKey = this.keyMaps['isLeaf']
            this.$set(this.treeNodeData[0], isLeafKey, true)
          }
          this.lazyLoadTreeNodeData = data.map(item => ({
            parentNode: {
              ...treeNode,
              childNodeCount: data.length
            },
            data: { ...item, isRoot: false },
            ...item
          }))
        }
      })
    },
    // 点击树节点
    handleClickNode(node, treeNodeIndex) {
      this.$emit('nodeClick', node, treeNodeIndex)
    },
    // 当前树节点改变复选框
    handleChangeNodeCheck(isChecked, node) {
      this.$emit('nodeCheck', isChecked, node)
    }
  },
  mounted() {
    // 懒加载情况下，初始加载level为0的树节点、即根节点
    if (this.level === 1 && this.isLazyLoad) {
      this.handleLazyLoad({ level: 0 })
    }
  }
}
</script>



