<template>
  <div class="pr-switch"
    :class="{ 'pr-switch-checked': isOn, 'pr-switch-disabled': disabled }"
    @click="handleChangeChecked">
    <span class="pr-switch-inner">
      {{ text }}
    </span>
  </div>
</template>

<script>
import '../theme-default/switch.less'
export default {
  name: 'pr-switch',
  model: {
    prop: 'isOn',
    event: 'change'
  },
  props: {
    text: {
      type: String,
      default: ''
    },
    isOn: {
      type: [Boolean, Number],
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  methods: {
    handleChangeChecked() {
      if (this.disabled) return
      this.$emit('change', !this.isOn)
    }
  }
}
</script>



