<template>
  <div ref="prTable"
    class="pr-table"
    :class="{
      'pr-table-border': border && !noBorder,
      'pr-table-no-border': noBorder && !border,
    }"
    :style="{ width: this.width }"
    @drop="handleEndDrag">
    <div class="pr-table-wrapper"
      v-prloading="{ isLoading: loading, title: '数据加载中...' }">
      <div ref="prTableHeader"
        class="pr-table-header">
        <table :class="{ 'pr-table-fixed': scrollY }">
          <colgroup>
            <col v-if="selectable"
              width="50" />
            <col v-if="draggable && hasTableData"
              width="30" />
            <col v-for="col in columns"
              :key="col.dataIndex"
              :width="col.width || columnWidth" />
            <col v-if="hasScroller && calcScrollerWidth > 0"
              :width="calcScrollerWidth" />
          </colgroup>
          <thead>
            <tr v-clickoutside="handleClose">
              <th v-if="selectable"
                width="50"
                align="left">
                <pr-checkbox v-model="allChecked"
                  :disabled="!tableData.length"
                  @change="handleCheckAll" />
              </th>
              <th v-if="draggable && hasTableData"
                width="30"
                class="pr-table-dragger"
                :style="{ padding: '0 0 0 10px' }"
                align="left"></th>
              <th v-for="(col, colIndex) in columns"
                :key="col.dataIndex"
                :width="col.width || columnWidth"
                :align="col.align || 'left'">
                <span class="pr-table-title"
                  :class="{
                    'pr-pointer': col.sortedBy || col.dropdownMenu,
                    'pr-active':
                      dropdownMenu[col.dataIndex] &&
                      dropdownMenu[col.dataIndex].activeIndex,
                  }"
                  :style="setTableHeaderStyle(col)"
                  @click="handleColumnEvent(colIndex, col)">
                  <slot name="head"
                    :column="col"
                    :index="colIndex">
                    {{ col.title }}
                  </slot>
                  <pr-icon v-if="col.dropdownMenu"
                    type="caret-down"
                    class="icon-for-dropdown"
                    :class="{
                      'pr-visible': dropdownMenu[col.dataIndex].visible,
                    }" />
                  <div class="pr-table-dropdown"
                    v-if="col.dropdownMenu"
                    v-show="dropdownMenu[col.dataIndex].visible">
                    <ul class="pr-table-dropdown-menu">
                      <li v-for="(menu, menuIdx) in col.dropdownMenu"
                        :key="menuIdx"
                        :class="{
                          'pr-active':
                            dropdownMenu[col.dataIndex].activeIndex === menuIdx,
                        }"
                        @click="handleSelectMenu(menu, col, colIndex, menuIdx)">
                        {{ menu.title }}
                      </li>
                    </ul>
                  </div>
                </span>
                <span v-if="col.sortedBy"
                  class="pr-table-sortable"
                  @click="handleColumnEvent(colIndex, col)">
                  <pr-icon type="caret-up"
                    :class="{ 'pr-ascend': col.sortedBy === 'asc' }" />
                  <pr-icon type="caret-down"
                    :class="{ 'pr-descend': col.sortedBy === 'desc' }" />
                </span>
              </th>
              <th v-if="hasScroller && calcScrollerWidth > 0"
                :width="calcScrollerWidth"></th>
            </tr>
          </thead>
          <tbody v-if="!scrollY && !hasTableData">
            <tr>
              <td :colspan="calcColspan"
                class="pr-table-empty">
                暂无数据
              </td>
            </tr>
          </tbody>
          <tbody v-if="!scrollY && hasTableData">
            <tr v-for="(item, idx) in tableData"
              :key="idx"
              :class="{ 'pr-selected': rowsChecked[idx].checked }"
              @click="handleRowClick(item, idx)"
              :draggable="draggable && dragger.index === idx"
              @dragstart="(e) => handleStartDrag(e, item, idx)"
              @dragenter="handleRowDragging"
              @dragend="handleEndDrag">
              <td v-if="selectable"
                width="50"
                align="left">
                <pr-checkbox v-model="rowsChecked[idx].checked"
                  @change="handleCheckList(idx)" />
              </td>
              <td v-if="draggable && hasTableData"
                width="30"
                class="pr-table-dragger"
                :style="{ padding: '0 0 0 10px' }"
                align="left">
                <span @mousedown="dragger.index = idx">
                  <pr-icon type="draggable" />
                </span>
              </td>
              <td v-for="(col, i) in columns"
                :key="col.dataIndex"
                :width="col.width || columnWidth"
                :align="col.align || 'left'">
                <render-node v-if="typeof col.render === 'function'"
                  :render-content="col.render"
                  :node="{
                    data: item,
                    column: col,
                    rowIndex: idx,
                    colIndex: i,
                  }"
                  :key="idx" />
                <slot v-else
                  :name="col.slot || col.dataIndex"
                  :column="col"
                  :data="item"
                  :rowIndex="idx"
                  :colIndex="i">
                  <div class="pr-ellipsis"
                    :title="initTitleProp(item, col, idx, i)">
                    {{ initTitleProp(item, col, idx, i) }}
                    <!-- {{col.formatter ? col.formatter(item[col.dataIndex], item) : item[col.dataIndex]}} -->
                    <!-- <span v-else
                                            v-html="col.formatter ? col.formatter(item[col.dataIndex]) : item[col.dataIndex]">
                                        </span> -->
                  </div>
                </slot>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- 固定表头 -->
      <div v-if="scrollY"
        ref="fixedTableBody"
        class="pr-table-body"
        :style="{ height: adaptingHeight || scrollY }">
        <table ref="fixedTable"
          :class="{ 'pr-table-fixed': scrollY }">
          <colgroup>
            <col v-if="selectable"
              width="50" />
            <col v-if="draggable"
              width="30" />
            <col v-for="col in columns"
              :key="col.dataIndex"
              :width="col.width || columnWidth" />
          </colgroup>
          <tbody v-if="!hasTableData">
            <tr>
              <td :colspan="calcColspan"
                class="pr-table-empty">
                暂无数据
              </td>
            </tr>
          </tbody>
          <tbody>
            <tr v-for="(item, idx) in tableData"
              :key="idx"
              :class="{ 'pr-selected': rowsChecked[idx].checked }"
              @click="handleRowClick(item, idx)"
              :draggable="draggable && dragger.index === idx"
              @dragstart="(e) => handleStartDrag(e, item, idx)"
              @dragenter="handleRowDragging"
              @dragend="handleEndDrag">
              <td v-if="selectable"
                width="50"
                align="left">
                <pr-checkbox v-model="rowsChecked[idx].checked"
                  @change="handleCheckList(idx)" />
              </td>
              <td v-if="draggable && hasTableData"
                width="30"
                class="pr-table-dragger"
                :style="{ padding: '0 0 0 10px' }"
                align="left">
                <span @mousedown="dragger.index = idx">
                  <pr-icon type="draggable" />
                </span>
              </td>
              <td v-for="(col, i) in columns"
                :key="col.dataIndex"
                :width="col.width || columnWidth"
                :align="col.align || 'left'">
                <render-node v-if="typeof col.render === 'function'"
                  :render-content="col.render"
                  :node="{
                    data: item,
                    column: col,
                    rowIndex: idx,
                    colIndex: i,
                  }"
                  :key="idx" />
                <slot v-else
                  :name="col.slot || col.dataIndex"
                  :column="col"
                  :data="item"
                  :rowIndex="idx"
                  :colIndex="i">
                  <div class="pr-ellipsis"
                    :title="initTitleProp(item, col, idx, i)">
                    {{ initTitleProp(item, col, idx, i) }}
                    <!-- {{col.formatter ? col.formatter(item[col.dataIndex], item) : item[col.dataIndex]}} -->
                    <!-- <span v-else
                                            v-html="col.formatter ? col.formatter(item[col.dataIndex]) : item[col.dataIndex]">
                                        </span> -->
                  </div>
                </slot>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <slot name="footer">
      <div ref="prTableFooter"
        v-if="pagination && hasTableData"
        class="pr-table-footer">
        <pr-pagination :up="pagination.up"
          :total="pagination.total"
          :current-page="pagination.currentPage"
          :show-total="pagination.showTotal"
          :page-size-options="pagination.pageSizeOptions"
          :page-size="pagination.pageSize"
          :show-page-size="pagination.showPageSize"
          :show-quick-jumper="pagination.showQuickJumper"
          @page-change="handleChangePage" />
      </div>
    </slot>
  </div>
</template>

<script>
import '../theme-default/table.less'
import clickoutside from '../../src/directives/clickoutside'
import { bindEvent, removeEvent, isIE } from '../../src/utils/common'
import PrCheckbox from '../checkbox/checkbox'
import PrPagination from '../pagination/pagination'
export default {
  name: 'pr-table',
  directives: {
    clickoutside
  },
  components: {
    'pr-checkbox': PrCheckbox,
    'pr-pagination': PrPagination,
    RenderNode: {
      name: 'render-node',
      props: {
        renderContent: {
          type: Function,
          default: null
        },
        node: {
          require: true
        }
      },
      render(h) {
        if (!this.renderContent) return null
        const node = this.node
        const { data, column, rowIndex, colIndex } = node
        return this.renderContent.call(this._renderProxy, h, {
          context: this.$vnode.context,
          node,
          data,
          column,
          rowIndex,
          colIndex
        })
      }
    }
  },
  props: {
    // 表格列目
    columns: {
      type: Array,
      default() {
        return []
      },
      require: true
    },
    // 表格数据
    dataSource: {
      type: Array,
      default() {
        return []
      },
      require: true
    },
    dropdownCache: {
      type: Array,
      default() {
        return []
      }
    },
    // 带边框
    border: {
      type: Boolean,
      default: false
    },
    // 无边框
    noBorder: {
      type: Boolean,
      default: false
    },
    // 表格宽度
    width: {
      type: String,
      default: null
    },
    // 加载状态
    loading: {
      type: Boolean,
      default: false
    },
    // 可否拖曳
    draggable: {
      type: Boolean,
      default: false
    },
    // 是否可选
    selectable: {
      type: Boolean,
      default: false
    },
    // 分页显示
    pagination: {
      type: Object,
      default: null
    },
    // 表格出现滚动条高度
    scrollY: {
      type: [String, Boolean],
      default: false
    }
  },
  data() {
    const dropdownMenu = {}
    for (let column of this.columns) {
      if (column.dropdownMenu) {
        dropdownMenu[column.dataIndex] = {
          visible: false,
          activeIndex: null
        }
      }
    }
    for (let cache of this.dropdownCache) {
      dropdownMenu[cache.dataIndex].activeIndex = cache.menuIndex
    }
    return {
      // 表格内容自适应高
      adaptingHeight: null,
      // 表格数据，不直接操作dataSource
      tableData: [...this.dataSource],
      // 是否全选
      allChecked: false,
      // 表格数据行checkbox状态
      rowsChecked: this.dataSource.map(item => ({
        ...item,
        checked: false
      })),
      // 表格下拉菜单
      // dropdownMenu: this.columns.map(col => {
      //   return {
      //     // 显示隐藏
      //     visible: false,
      //     // 选中菜单选项
      //     activeIndex: null
      //   }
      // }),
      dropdownMenu,
      // 表格宽
      tableWidth: 0,
      // 列宽
      columnWidth: 0,
      // 拖动行数据
      dragger: {
        data: null,
        index: null
      },
      // 是否有滚动条（解决原生滚动条影响表格对不齐问题）
      hasScroller: false
    }
  },
  computed: {
    /**
     * 暂无数据时单元格占列
     */
    calcColspan() {
      let colspan = this.columns.length
      this.selectable && colspan++
      this.draggable && colspan++
      return colspan
    },
    /**
     * 计算滚动条宽度
     */
    calcScrollerWidth() {
      if (this.scrollY && this.hasScroller) {
        let tableBodyWidth = this.$refs.fixedTableBody.offsetWidth
        let tableWidth = this.$refs.fixedTable.offsetWidth
        return tableBodyWidth - tableWidth - 1
      }
    },
    /**
     * 判断表格数据是否为空
     */
    hasTableData() {
      return this.tableData.length > 0
    }
  },
  methods: {
    /**
     * 设置title属性
     */
    initTitleProp(data, column, rowIndex, colIndex) {
      if (column.formatter) {
        return column.formatter(data[column.dataIndex], {
          data,
          column,
          rowIndex,
          colIndex
        })
      } else {
        return data[column.dataIndex]
      }
    },
    handleRowClick(rowData, rowIndex) {
      this.$emit('row-click', rowData, rowIndex)
    },
    /**
     * 开始拖曳
     */
    handleStartDrag(e, data, index) {
      if (!this.draggable) return
      this.dragger = {
        data,
        index
      }
      this.$emit('start-drag', e, data, index)
    },
    /**
     * 结束拖曳
     */
    handleEndDrag(e) {
      if (!this.draggable) return
      e.preventDefault()
      const { data, index } = this.dragger
      this.$emit('end-drag', e, data, index, this.tableData)
      this.$nextTick(() => {
        this.dragger = {
          data: null,
          index: null
        }
      })
    },
    /**
     * 拖曳中...
     */
    handleRowDragging(e) {
      if (!this.draggable) return
      e.preventDefault()
      let { data, index } = this.dragger
      if (!data) return
      // 表格行索引
      let rowIndex = e.currentTarget.rowIndex - (this.scrollY ? 0 : 1)
      if (index !== rowIndex) {
        let tableData = [...this.tableData]
        ;[tableData[index], tableData[rowIndex]] = [
          tableData[rowIndex],
          tableData[index]
        ]
        this.tableData = [...tableData]
        this.dragger.index = rowIndex
      }
      this.$emit('dragging', e, data, index)
    },
    /**
     * 有 下拉或排序 则宽度自动
     * 否则继承父元素宽度
     * 用于文本溢出...
     * @param {Object} column
     */
    setTableHeaderStyle(column) {
      let width = column.dropdownMenu || column.sortedBy ? 'auto' : ''
      let overflow = column.dropdownMenu ? 'visible' : 'overflow'
      return {
        width,
        overflow
      }
    },
    /**
     * 分页事件
     * @param pageArgs {currentPage, pageSize}
     */
    handleChangePage(pageArgs) {
      this.$emit('page-change', pageArgs)
    },
    /**
     * 全(不?)选
     */
    handleCheckAll() {
      this.rowsChecked.forEach(item => {
        item.checked = this.allChecked
      })
      this.$emit('checked-all', this.allChecked, this.rowsChecked)
    },
    /**
     * 供外部调用
     * 取消全选状态
     * @param {Boolean} isAllChecked
     */
    toggleCheckAll(isAllChecked = false) {
      this.allChecked = isAllChecked
      for (let item of this.rowsChecked) {
        item.checked = isAllChecked
      }
    },
    /**
     * 供外部调用
     * 默认选中行
     * @param {Array} rows 选中的数据
     * @param {String} query 唯一索引
     */
    toggleCheckRows(rows, query) {
      if (rows && rows instanceof Array) {
        let isAllChecked = true
        for (let item of this.rowsChecked) {
          for (let row of rows) {
            row.checked = item.checked
            if (item[query] === row[query]) {
              item.checked = !item.checked
            }
          }
          if (!item.checked) {
            isAllChecked = false
          }
        }
        this.allChecked = isAllChecked
      } else {
        this.toggleCheckAll()
      }
    },
    /**
     * 选择列表项
     * @param idx{Number} 选中行索引
     */
    handleCheckList(idx) {
      let isAllChecked = true
      for (let item of this.rowsChecked) {
        if (!item.checked) {
          isAllChecked = false
          break
        }
      }
      this.allChecked = isAllChecked
      this.$emit('checked-change', this.rowsChecked[idx], this.rowsChecked)
    },
    /**
     * 升降排序事件
     * @param column 列数据
     */
    handleSort(column) {
      const sortedMap = {
        none: 'asc',
        asc: 'desc',
        desc: 'none'
      }
      const nextSortedBy = column.sortedBy
      column.sortedBy = sortedMap[nextSortedBy]
      this.$emit('sort-change', column, sortedMap[nextSortedBy])
    },
    handleDropdown(column) {
      const visible = this.dropdownMenu[column.dataIndex].visible
      this.handleClose()
      if (!visible) {
        this.dropdownMenu[column.dataIndex].visible = !visible
      }
    },
    /**
     * 表头下拉菜单
     * @param idx 列索引
     * @param column 列数据
     */
    handleColumnEvent(idx, column) {
      if (column.dropdownMenu) {
        this.handleDropdown(column)
      }
      if (column.sortedBy) {
        this.handleSort(column)
      }
    },
    /**
     * 表头下拉菜单选择事件
     * @param {Object} menu
     * @param {Object} col
     * @param {Number} colIndex
     * @param {Number} menuIdx
     */
    handleSelectMenu(menu, col, colIndex, menuIdx) {
      // this.dropdownCache[colIndex] = {
      //   dataIndex: col.dataIndex,
      //   menuIndex: menuIdx
      // }
      this.dropdownMenu[col.dataIndex].activeIndex = menuIdx
      menu.fun && menu.fun(col, menuIdx)
    },
    /**
     * click-outside事件
     */
    handleClose() {
      // this.dropdownMenu = this.dropdownMenu.keys().map(menu => {
      //   menu.visible = false
      //   return menu
      // })
      Object.keys(this.dropdownMenu).map(key => {
        this.dropdownMenu[key].visible = false
      })
    },
    /**
     * 防止ie下拖动时选中内容
     * @param {event} e
     */
    preventSelectInIE(e) {
      for (let el = e.target; el; el = el.parentNode) {
        if (el.attributes && el.attributes['draggable']) {
          e.preventDefault()
          e.stopImmediatePropagation()
          el.dragDrop()
          return false
        }
      }
    },
    /**
     * 特殊需求
     * 计算列宽（减去前后固定宽度后剩余平分）
     */
    calcTableWidth() {
      const { draggable, selectable, columns } = this
      let length = 0
      const specifyWidth = columns
        .map(column => {
          if (column.width) length++
          return column.width ? parseFloat(column.width) : 0
        })
        .reduce((prevItem, nextItem) => prevItem + nextItem)
      // 拖动固宽
      const draggerWidth = 50
      // 多选固宽
      const checkboxWidth = 50
      // 单元格左距
      const cellPadding = 30

      let columnWidth = null
      let tableWidth =
        (this.width || (this.$el && this.$el.offsetWidth) || 0) - specifyWidth
      if (!selectable) {
        if (!draggable) {
          columnWidth = tableWidth / (columns.length - length)
        } else {
          columnWidth = (tableWidth - draggerWidth) / (columns.length - length)
        }
      } else {
        if (!draggable) {
          columnWidth = (tableWidth - checkboxWidth) / (columns.length - length)
        } else {
          columnWidth =
            (tableWidth - checkboxWidth - draggerWidth) /
            (columns.length - length)
        }
      }
      this.tableWidth = tableWidth + 'px'
      this.columnWidth = columnWidth - cellPadding
      // if (specifyWidth) {
      //   this.columnWidth = Math.min(columnWidth - cellPadding, specifyWidth)
      // } else {
      //   this.columnWidth = columnWidth - cellPadding
      // }
    },
    /**
     * 计算表格自适应高
     * 高度根据项目需要计算，不适用其他项目场景
     */
    calcTableScrollHeight() {
      // if (this.scrollY && this.scrollY !== 'auto') return
      let ieFooterHeight = isIE() ? 20 : 0
      let computedStyle
      let tableFooterHeight = 0
      let tableHeaderHeight = this.$refs.prTableHeader.offsetHeight
      if (this.pagination && this.hasTableData) {
        computedStyle = window.getComputedStyle(this.$refs.prTableFooter)
        tableFooterHeight =
          parseInt(computedStyle.height) +
          parseInt(computedStyle.paddingTop) * 2
      }
      // let toBottom = parseInt(computedStyle.paddingTop)
      // top => 表格距离顶部距离
      const { top } = this.$refs.prTable.getBoundingClientRect()
      // 窗口高度
      const clientHeight =
        document.documentElement.clientHeight || document.body.clientHeight
      this.adaptingHeight = `${clientHeight -
        top -
        tableHeaderHeight -
        tableFooterHeight -
        ieFooterHeight}px`
    },
    resizeToCalc() {
      this.calcTableWidth()
      if (this.scrollY === 'auto') {
        this.calcTableScrollHeight()
      }
    }
  },
  watch: {
    dataSource(newDataSource) {
      this.calcTableWidth()
      this.tableData = [...newDataSource]
      this.allChecked = false
      this.rowsChecked = newDataSource.map(item => ({
        ...item,
        checked: false
      }))
      if (this.scrollY) {
        this.$nextTick(() => {
          this.$refs.fixedTableBody.scrollTop = 0
        })
      }
    },
    dropdownCache(newCache) {
      for (let cache of this.dropdownCache) {
        this.dropdownMenu[cache.dataIndex].activeIndex = cache.menuIndex
      }
    }
  },
  mounted() {
    this.calcTableWidth()
    bindEvent(window, 'resize', this.resizeToCalc)
    // ie9 拖动兼容
    // if (document.doctype && navigator.appVersion.indexOf('MSIE 9') > -1) {
    //   bindEvent(document, 'selectstart', this.preventSelectInIE)
    // }
    if (isIE()) {
      bindEvent(document, 'selectstart', this.preventSelectInIE)
    }
  },
  updated() {
    if (this.$refs.fixedTable && this.$refs.fixedTableBody) {
      let tableBodyHeight = this.$refs.fixedTableBody.offsetHeight
      let tableHeight = this.$refs.fixedTable.offsetHeight
      this.hasScroller = tableBodyHeight < tableHeight
    }
    if (
      this.scrollY === 'auto' &&
      !this.adaptingHeight &&
      this.tableData.length
    ) {
      this.calcTableScrollHeight()
    }
  },
  beforeDestroy() {
    removeEvent(window, 'resize', this.resizeToCalc)
    removeEvent(document, 'selectstart', this.preventSelectInIE)
  }
}
</script>



