<template>
  <div class="pr-collapse-item hairline-bottom"
    :class="{ 'pr-uncollapsed': uncollapsed }">
    <div class="pr-collapse-title"
      @click="toggleCollapse">
      <div class="pr-collapse-title-desc">
        <slot name="title">{{ title }}</slot>
      </div>
      <span class="pr-collapse-title-icon">
        <slot name="icon">
          <i class="pr-icon iconfont pr-icon-angle-single-down"></i>
        </slot>
      </span>
    </div>
    <div class="pr-collapse-content">
      <div class="pr-collapse-content-compensate">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import '../theme-default/collapse-item.less'
export default {
  name: 'pr-collapse-item',
  props: {
    title: {
      type: [String, Number, Boolean],
      default: null
    },
    name: [String, Number]
  },
  computed: {
    uncollapsed() {
      const value = this.$parent.value
      return this.$parent.accordion
        ? this.name === value
        : value.includes(this.name)
    }
  },
  methods: {
    toggleCollapse() {
      let value = null
      if (this.$parent.accordion) {
        value = this.$parent.value === this.name ? '' : this.name
      } else {
        value = [...this.$parent.value]
        if (!this.uncollapsed) {
          value.push(this.name)
        } else {
          value = value.filter(item => item !== this.name)
        }
      }

      this.$parent.$emit('change', value)
    }
  }
}
</script>



