<template>
  <transition name="fade">
    <div v-if="visible"
      class="pr-mask"
      @click="handleClose"></div>
  </transition>
</template>

<script>
export default {
  name: 'pr-mask',
  data() {
    return {
      visible: false,
      closableOverlay: false
    }
  },
  methods: {
    handleClose() {
      if (this.closableOverlay) {
        this.onClickOverlay && this.onClickOverlay()
        this.visible = false
      }
    }
  }
}
</script>



